<template>
    <div>
        <div class="box-container">
            <div class="box-btn">
                <h4>平台数据和模型使用记录</h4>
            </div>
            <!-- 数据部分 -->
            <el-table
                v-loading="loading"
                :data="recordList"
                style="width: 100%;margin-bottom: 20px;"
                >
                <el-table-column type="expand" label="计算">
                    <template slot-scope="props">
                        <div class="comp-box">
                            <div>
                                <h4>密钥</h4>
                                {{ JSON.parse(props.row.params).publicKey }}
                            </div>
                            <div class="text">
                                <h4>数据A</h4>
                                <div class="now-btn">
                                    <el-button round size="small" type="primary" @click="getNowCodeA(props)">获取明文数据A</el-button>
                                </div>
                                {{JSON.parse(props.row.params).A }}
                                
                            </div>
                            <div>
                                <h4>数据B</h4>
                                <div class="now-btn">
                                    <el-button round size="small" type="primary" @click="getNowCodeB(props)">获取明文数据B</el-button>
                                </div>
                                {{JSON.parse(props.row.params).B }}
                            </div>
                            <div>
                                <h4>计算结果</h4>
                                {{ props.row.result }}
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="username" align="center" label="使用用户"></el-table-column>
                <el-table-column prop="startTime" align="center" label="使用时间"></el-table-column>
            </el-table>
        </div>
        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>
        <!-- 明文结果A -->
        <el-dialog title="明文数据A"
            @close="closeDialogA"
            :visible.sync="dialogFormVisibleA_">
            <el-form :model="now">
                <el-form-item :label-width="formLabelWidth">
                    <span>{{ now.dataA}}</span>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!-- 明文结果B -->
        <el-dialog title="明文数据B"
            @close="closeDialogB"
            :visible.sync="dialogFormVisibleB_">
            <el-form :model="now">
                <el-form-item :label-width="formLabelWidth">
                    <span>{{ now.dataB}}</span>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
export default {
    components:{
        Pagination
    },
    data(){
        return{
            dialogFormVisibleA_:false,
            dialogFormVisibleB_:false,
            formLabelWidth:'50px',
            loading:false,
            recordList:[],
            username:'',
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            },
            now:{
                dataA:'',
                dataB:''
            }
        }
    },
    methods:{
        getRecordList(){//获取用户使用数据
            let that = this;
            that.loading = true;
            this.$api.admrelist({
                page_num: that.query.pageIndex,
                page_size: that.query.pageSize,
                username: that.username
            }).then(res=>{
                let data = res.data.records;
                that.recordList = data.content;
                that.query.total = data.totalElements;
                that.query.pageSize = data.size;
                that.loading = false;
            })
        },
        getSize(size){//底部导航
            this.query.pageSize = size;
            this.getRecordList();
        },
        getIndex(index){
            this.query.pageIndex = index;
            this.getRecordList();
        },
        getNowCodeA(data){//显示明文数据A
            this.dialogFormVisibleA_=true;
            let nowdata = JSON.parse(data.row.params),
                username =nowdata.username,
                dataA = nowdata.A,
                key = nowdata.publicKey,
                that= this;
            this.$api.admnowcode({
                code: dataA,
                key: key,
                username:username
            }).then(res=>{
                that.now.dataA = res;
            })
        },
        getNowCodeB(data){//显示明文数据B
            this.dialogFormVisibleB_=true;
            let nowdata = JSON.parse(data.row.params),
                username =nowdata.username,
                dataB = nowdata.B,
                key = nowdata.publicKey,
                that =this;
            this.$api.admnowcode({
                code: dataB,
                key: key,
                username:username
            }).then(res=>{
                that.now.dataB = res;
            })
        },
        closeDialogA(){
            this.now.dataA = '';
        },
        closeDialogB(){
            this.now.dataB = '';
        }
    },
    created(){
        this.getRecordList();
    }
}
</script>

<style lang="scss">
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
    .comp-box{
        width: 100%;
        height: 100%;
        overflow-x: scroll;
    }
    .now-btn{
        .el-button{
            margin: 5px 10px;
        }
        .now-text{
            color: red;
        }
    }
</style>